import React, { useState, useEffect } from 'react';
import './style.scss';

import LinkButton from '../link-button';

import actions from '../../actions';
import { connect } from 'react-redux';

const WrStBtn = ({ setNextRoute }) => {

    const handleClick = () => {
        setNextRoute('/write-story');
    }

    return (
        <LinkButton text="Почати розповідь" linkTo="/write-story" className="blue-btn" onClick={handleClick} />
    )
}

const WriteStoryButton = connect(() => ({}), actions)(
    WrStBtn
)


const GenreButton = connect(() => ({}), actions)(
    ({ text, id, setFirstStoryGenresList }) => {

        const [active, setActive] = useState(false);

        const onClick = () => {
            setFirstStoryGenresList(text);
            setActive(!active);
        }

        return (
            <button className={active ? "active" : ""} onClick={onClick}><span>{text}</span></button >
        )
    }
);

const Genres = connect(({ genresList }) => ({ genresList }), actions)(
    ({ genresList }) => {
        const [genresButtonList, setGenresButtonList] = useState([]);

        useEffect(() => {
            setGenresButtonList(genresList.map(({ _id, name }) =>
                (<GenreButton text={name} key={_id} id={_id} />)
            ))
        }, [genresList])

        return (
            <div className="genres">
                <h1>Не бійся показати талант <br /> усьому світові!</h1>
                <div className="genres-list">
                    {genresButtonList}
                </div>
                <h3>
                    В яких жанрах ти розпочнеш майбутню розповідь?
                </h3>
                <WriteStoryButton />
            </div>
        )
    }
);


class About extends React.Component {

    componentDidMount = () => {
        document.title = 'Ласкаво просимо до Storyteller';
    }

    render() {

        return (
            <div className="about">
                <Genres />
                <div className="main">
                    <section className='your-story'>
                        <div className="text">
                            <h2>Напиши<br />свою історію!</h2>
                            <p>
                                Хочеш написати книгу або коротке
                                оповідання? А може ти поет або поетеса?
                    <span> Пам’ятай! У кожного є історія, якою
                    він хотів би поділитися!</span>
                            </p>
                        </div>
                    </section>
                    <section className='reverse chapter'>
                        <div className="text">
                            <h2>Пиши главу <br /> за главою.</h2>
                            <p>
                                Навіть якщо твоя розповідь ще не закінчена,
                                ти можеш публікувати її поступово, главу
                    за главую. <span>Твої підписники будуть
                    з нетерпінням чекати нових оновлень.</span>
                            </p>
                        </div>
                    </section>
                    <section className='feedback'>
                        <div className="text">
                            <h2>Отримуй “фідбек”.</h2>
                            <p>
                                Пиши розповіді, книги або вірші, отримуй
                                лайки, коментарі і безпосередньо спілкуйся
                                з шанувальниками свого таланту. Хочеш
                    заробляти на цьому? Легко!<span> Підписники
                                мають змогу матеріально підтримувати
                    улюблених авторів.</span>
                            </p>
                        </div>
                    </section>
                </div>
                <div className="bottom">
                    <h2>Розкрий свій талант.<br />Покажи свої думки.</h2>
                    <WriteStoryButton />
                </div>
            </div>
        )
    }
}

export default About;